  top: 4px;
  right: 4px;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 3;
  background: rgba(94, 99, 99, .7);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden.modal {
  transform: translate(-50%, -50%)scale(.8);
}

.modal {
  width: 100%;
  height: 449px;
  opacity: 1;
  background: #ffe550;
  border-radius: 30px;
  padding: 32px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

